<template>
    <div class="tracking">
        <div class="auto">
            <breadcrumb />
            <h2 class="title">訂單查詢</h2>
            <el-form ref="form" label-width="96px" :label-position="labelPosition">
                <el-form-item label="聯絡 E-mail">
                    <el-input v-model="email" class="input"></el-input>
                </el-form-item>
                <el-form-item label="乘車人">
                    <el-input v-model="user" class="input"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="success" @click="submit" class="btn">查詢</el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
import breadcrumb from '@/components/breadcrumb/index.vue';
export default {
    name: 'tracking',
    components: {
        breadcrumb
    },
    computed: {
        labelPosition() {
            if (this.$store.state.screenWidth < 1208) {
                return 'top';
            } else {
                return 'right';
            }
        },
        user: {
            get() {
                return this.$store.state.track.user;
            },
            set(val) {
                this.$store.commit('TRACKUSER', val);
            }
        },
        email: {
            get() {
                return this.$store.state.track.email;
            },
            set(val) {
                this.$store.commit('TRACKEMAIL', val);
            }
        }
    },
    methods: {
        submit() {}
    }
};
</script>
<style lang="scss" scoped>
.tracking {
    font-family: '微軟正黑體', sans-serif;
    background-color: #f1f1f1;
    .auto {
        width: 1440px;
        margin: auto;
        display: flex;
        flex-direction: column;
        @media screen and (max-width: 1440px) {
            width: 1224px;
        }
        @media screen and (max-width: 1224px) {
            width: 96%;
        }
        h2 {
            color: #999;
            font-size: 24px;
            line-height: 40px;
            font-weight: 700;
            padding: 0 0 0 16px;
            margin: 8px;
            position: relative;
            &:before {
                content: '';
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                margin: auto;
                background-color: #999;
                width: 4px;
                height: 24px;
            }
        }
        form {
            width: 800px;
            padding: 16px;
            margin: 0 0 40px 0;
            background: #f7f7f7;
            align-self: center;
            @media screen and (max-width: 1224px) {
                width: 96%;
            }
        }
    }
}
</style>