<template>
    <div class="breadcrumb">
        <ul>
            <i class="el-icon-s-home"></i>
            <li
                v-for="(breadcrumb, index) in breadcrumbList"
                :key="index"
                @click="routeTo(index)"
            >{{ breadcrumb.name }}</li>
        </ul>
    </div>
</template>

<script>
export default {
    name: 'breadcrumb',
    data() {
        return {
            breadcrumbList: []
        };
    },
    mounted() {
        this.updateList();
    },
    watch: {
        $route() {
            this.updateList();
        }
    },
    methods: {
        routeTo(pRouteTo) {
            if (this.breadcrumbList[pRouteTo].link) {
                this.$router.push(this.breadcrumbList[pRouteTo].link);
            }
        },
        updateList() {
            this.breadcrumbList = this.$route.meta.breadcrumb;
        }
    }
};
</script>

<style lang="scss" scoped>
.breadcrumb {
    border-top: 1px solid #dcdfe6;
    border-bottom: 1px solid #dcdfe6;
    color: $text;
    font-size: 14px;
    > ul {
        display: flex;
        line-height: 32px;
        > i {
            margin: 0 4px 0 0;
            line-height: 32px;
            color: $text;
        }
        > li {
            cursor: pointer;
            transition: 0.6s;
        }
        > li:not(:last-child):hover {
            color: $green;
        }
        > li:last-child {
            cursor: default;
            color: $deep-green;
        }
        > li:not(:last-child):after {
            content: '/';
            margin: 8px;
            color: $text;
        }
    }
}
@media screen and (min-width: 1200px) {
    .breadcrumb {
        max-width: 1200px;
        width: 100%;
    }
}
</style>
